
import { Component, Vue, Prop, Emit } from "vue-property-decorator";

// interface Item {
//   id: string;
//   isCompleted: boolean;
//   isShow:boolean,
//   content: string;
// }
@Component({
  components: {},
})
export default class extends Vue {
  // ----------------------Prop----------------------
  @Prop() item!: any;
  /** 是否处于可编辑状态 */
  @Prop() isOtherItemEditingStatus!: boolean;
  // ----------------------data----------------------
  isMouseEnterChangeColor = false;
  /** 是否处于编辑状态 */
  isEdit = false;
  /** 悬停展示编辑删除按钮 */
  isShowEditBtn = false;
  // ----------------------methods----------------------
  showEditBtn() {
    this.isShowEditBtn = true;
  }
  hideEditBtn() {
    this.isShowEditBtn = false;
  }

  @Emit("change-edit-status")
  editItem() {
    this.isEdit = true;
    return this.isEdit;
  }

  @Emit("delete-item")
  deleteItem() {
    return this.item.id;
  }

  @Emit("change-edit-status")
  finishEdit() {
    this.isEdit = false;
    return this.isEdit;
  }
  /**
   * @description: 鼠标进入后改变当前项目的背景颜色
   * @param {*} e 鼠标事件
   * @return {*}
   * @author:
   */
  mouseEnterChangeColor(e: any) {
    console.log(e.target.__vue__._props.item.id);
    if (e.target.__vue__._props.item.id === this.item.id) {
      this.isMouseEnterChangeColor = true;
    }
  }
  mouseLeaveChangeColor() {
    this.isMouseEnterChangeColor = false;
  }
}
